import {uniSegmentedControl} from 'components/uni-segmented-control/uni-segmented-control.vue'
	export default {
		components:{
			uniSegmentedControl
		},
		data() {
		        return {
		            items: [
						{title:"最新",order:"new"},
						{title:"热门",order:"hot"}						
					],
		            current: 0,
					params:{
						limit:30,
						skip:0,
						order:"new"
					},
					id:0,
					vertical:[],
					hasMore:true
		        };
		    },
		onLoad(options){
			this.id=options.id;
			this.getList();
		},
		methods: {
		    onClickItem(e) {
		        if (this.current !== e.currentIndex) {
		            this.current = e.currentIndex;
		        }else{
					//点击的标题相同
					return;
				}
				this.params.order=this.items[e.currentIndex].order;
				this.params.skip=0;
				this.vertical=[];
				this.getList();
		    },
			getList(){
				this.request({
					url:`http://157.122.54.189:9088/image/v1/vertical/category/${this.id}/vertical`,
					data:this.params
				}).then(result=>{
					console.log(result);
					if(result.res.vertical.length===0){
						this.hasMore=false;
						uni.showToast({
							title:"没有了",
							icon:"none"
						})
						return;
					}
					this.vertical=[...this.vertical,...result.res.vertical];
				})
			},
			handleScrolltoLower(){
				if(this.hasMore){
					this.params.skip+=this.params.limit;
					this.getList();
				}else{
					uni.showToast({
						title:"没有了",
						icon:"none"
					})
				}
			}
		}
	}
